import react, {useState} from 'react'
import headerImage from './Images/wineHeader.jpg'
import White9 from './Images/wine_white_9.jpg'
import White6 from './Images/wine_white_6.jpg'
import White3 from './Images/wine_white_3.jpg'
import Red9 from './Images/wine_red_9.jpg'
import Red6 from './Images/wine_red_6.jpg'
import Red3 from './Images/wine_red_3.jpg'
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import axios from 'axios';
import './App.css'

function App() {
  const [loading,setLoading]=useState(false);
  const [image,setImage]= useState(Red3)
  const [war, setWar]=useState(1);
  const [alcohol, setAlcohol]=useState(10.5);
  const [sulphates, setSulphates]=useState(0.5);
  const [ph, setPh]=useState(3.2);
  const [density, setDensity]=useState(0.99);
  const [totalsdo, setTotalsdo]=useState(115);
  const [freesdo, setFreesdo]=useState(30);
  const [chlorides, setChlorides]=useState(0.05);
  const [resisugar, setResisugar]=useState(5.5);
  const [citacid, setCitacid]=useState(0.3);
  const [volacid, setVolacid]=useState(0.38);
  const [fixacid, setFixacid]=useState(7.5);
 
  
  const warchangehandler=(slider)=>{
    setWar(slider);
  }
  const fixacidchangehandler=(slider)=>{
    setFixacid(slider);
  }
  const volacidchangehandler=(slider)=>{
    setVolacid(slider);
  }
  const citacidchangehandler=(cit)=>{
    setCitacid(cit);
  }
  const resisugarchangehandler=(slider)=>{
    setResisugar(slider);
  }
  const chlorideschangehandler=(slider)=>{
    setChlorides(slider);
  }
  const freesdochangehandler=(slider)=>{
    setFreesdo(slider);
  }
  const totalsdochangehandler=(slider)=>{
    setTotalsdo(slider);
  }
  const densitychangehandler=(slider)=>{
    setDensity(slider);
  }
  const phchangehandler=(slider)=>{
    setPh(slider);
  }
  const alcoholchangehandler=(slider)=>{
    setAlcohol(slider);
  }
  const sulphateschangehandler=(slider)=>{
    setSulphates(slider);
  }
  
const sendRequestHandler=()=>{
  setLoading(true)
  const data={
    war,
    fixacid,
    volacid,
    citacid,
    resisugar,
    chlorides,
    freesdo,
    totalsdo,
    density,
    ph,
    sulphates,
    alcohol
  }
  axios.post("/hello",data)
  .then(res=>{
    
    setLoading(false)
    if(war===0){
      if(res.data===3){
        setImage(White3)
      }
      if(res.data===6){
        setImage(White6)
      }
      if(res.data===9){
        setImage(White9)
      }
    }
    if(war===1){
      if(res.data===3){
        setImage(Red3)
      }
      if(res.data===6){
        setImage(Red6)
      }
      if(res.data===9){
        setImage(Red9)
      }
    }
    
  })
  .catch(error=>{
    console.log(error)
    console.log("hh")
  })
}
  return (
    <div className="App">
      <div className="header">
        <img className="headerImages" src={headerImage} alt="headerImage"/>
      </div>
      <div className="headerLine">
        <h3>Customize your wine parameters and get real-time quality predictions!</h3>
      </div>
      <div className="allLabels">
        <div className="firstcol">
          <div className="item">
          <div>white or red :{war}</div>
            <Slider
            min={0}
            max={1}
            step={1}
            value={war}
            onChange={warchangehandler} />
            
        </div>
        <div className="item">
          <div>Fixed Acidity: {fixacid}</div>
          <Slider
            min={3.5}
            max={16.5}
            step={0.1}
            value={fixacid}
            onChange={fixacidchangehandler} />
            
        </div>
        <div className="item">
          <div>Volatile Acidity: {volacid}</div>
          <Slider
            min={0.08}
            max={1.60}
            step={0.01}
            value={volacid}
            onChange={volacidchangehandler} />
            
        </div>
        <div className="item">
          <div>Citric Acid: {citacid}</div>
          <Slider
            min={0.0}
            max={1.6}
            step={0.1}
            value={citacid}
            onChange={citacidchangehandler} />
            
        </div>
        <div className="item">
          <div>Residual Sugar: {resisugar}</div>
          <Slider
            min={0.5}
            max={66.5}
            step={0.5}
            value={resisugar}
            onChange={resisugarchangehandler} />
            
        </div>
        <div className="item">
          <div>Chlorides: {chlorides}</div>
          <Slider
            min={0.01}
            max={0.62}
            step={0.01}
            value={chlorides}
            onChange={chlorideschangehandler} />
            
        </div>
        </div>
        <div className="secondcol">
          {
            loading?<div className="Loader">Loading...</div>:
            <img className="winepic" src={image} alt="pic of wine"/>
          }
          
        </div>
        <div className="thirdcol">
          <div className="item">
          <div>Free Sulfur Dioxide: {freesdo}</div>
          <Slider
            min={1}
            max={291}
            value={freesdo}
            step={1}
            onChange={freesdochangehandler} />
            
        </div>
        <div className="item">
          <div>Total Sulfur Dioxide: {totalsdo}</div>
          <Slider
            min={5}
            max={440}
            step={1}
            value={totalsdo}
            onChange={totalsdochangehandler} />
            
        </div>
        <div className="item">
          <div>Density: {density}</div>
          <Slider
            min={0.98}
            max={1.04}
            step={0.01}
            value={density}
            onChange={densitychangehandler} />
            
        </div>
        <div className="item">
          <div>pH: {ph}</div>
          <Slider
            min={2.7}
            max={4.1}
            step={0.1}
            value={ph}
            onChange={phchangehandler} />
            
        </div>
        <div className="item">
          <div>Sulphates: {sulphates}</div>
          <Slider
            min={0.2}
            max={2.0}
            step={0.1}
            value={sulphates}
            onChange={sulphateschangehandler} />
            
        </div>
        <div className="item">
          <div>Alcohol: {alcohol}</div>
          <Slider
            min={8.0}
            max={15.0}
            step={0.1}
            value={alcohol}
            onChange={alcoholchangehandler} />
            
        </div>
        </div>
        </div>
        <div className= "sendButt">
        <button  onClick={sendRequestHandler}>Predict</button>
        </div>
       
    </div>
    
  );
}

export default App;
